@import "../../variables.scss";
.infiniteScrollingImages {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.following-image {
  height: 20rem;
  width: 30rem;
  background-color: $white;
  opacity: 1;
  z-index: -1;
  border: 1px solid $black;
  box-shadow: 0 0 10px 10px $white;
  transition: opacity 2s;

  img {
    width: 100%;
    height: 100%;
    z-index: 30;
  }
}
